import { GoogleLogin } from "react-google-login";

const clientId = "608309846684-kgos87bdmns6f72prcgrk5c68a9h6kbd.apps.googleusercontent.com"

function Login(){
    const onSuccess = (res)=>{
        console.log("Success login, Current user : ", res.profileObj );
    }
    const onFailure = (err)=>{
        console.log("Failled login, err: ", err );
    }

    return(
        <div>
            <GoogleLogin
                clientId={clientId}
                buttonText={"Login"}
                onSuccess={onSuccess}
                onFailure={onFailure}
                cookiePolicy={'single_host_origin'}
                isSignedIn={true}
            />
        </div>
    )
}

export default Login